@use '../foundation' as *;

.p-privacy-policy {
    padding: rem(80) 0;
    @include mq(md){
        padding: rem(120) 0;
    }
}

.p-privacy-policy__inner {
    margin: auto;
    padding: 0 rem(20);
    max-width: rem(600);
    width: 100%;
    @include mq(md){
        padding: 0 rem(25);
        max-width: rem(1200);
    }
}

.p-privacy-policy__title {
    margin-bottom: rem(30);
    font-weight: 400;
    @include mq(md){
        margin-bottom: rem(40);
    }
}

.p-privacy-policy__introduction {
    font-size: rem(16);
    line-height: 2;
    @include mq(md){
        font-size: rem(18);
    }
}

.p-privacy-policy__items {
    margin-top: rem(32);
    padding-top: rem(20);
    border-top: 1px solid #ccc;
    @include mq(md){
        margin-top: rem(40);
        padding-top: rem(40);
    }
}

.p-privacy-policy__item {
    padding-bottom: rem(20);
    border-bottom: 1px solid #ccc;
    @include mq(md){
        padding-bottom: rem(40);
    }
}

.p-privacy-policy__item + .p-privacy-policy__item {
    margin-top: rem(20);
    @include mq(md){
        margin-top: rem(40);
    }
}

.p-privacy-policy__term {
    font-size: rem(18);
    font-weight: 700;
    line-height: 1.5;
    @include mq(md){
        font-size: rem(20);
    }
}

.p-privacy-policy__description {
    margin-top: rem(15);
    font-size: rem(16);
    line-height: 2;
    @include mq(md){
        font-size: rem(18);
    }
}

.p-privacy-policy__description-numbers {
    counter-reset: number;
}

.p-privacy-policy__description-text + .p-privacy-policy__description-numbers,
.p-privacy-policy__description-text + .p-privacy-policy__description-items {
    margin-top: rem(15);
}

.p-privacy-policy__description-number {
    padding-left: rem(15);
    text-indent: rem(-15);
}

.p-privacy-policy__description-number + .p-privacy-policy__description-number,
.p-privacy-policy__description-item + .p-privacy-policy__description-item {
    margin-top: rem(15);
}

.p-privacy-policy__description-number::before {
    content: counter(number) ". ";
    counter-increment: number;
}

.p-privacy-policy__description-items {
    padding-left: rem(20);
    list-style: disc;
}
